.App-header {
    position: fixed;
    width: 100%;
    background-color: #282c34;
    min-height: 100vh;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    overflow: hidden;
    transition: height 1s ease-in-out;
}

.mess_orientation {
    position: absolute;
    right: 0;
    top: 20vh;
    display: none;
    /* color: black; */
    background-color: red;
    color: white;
}

@media screen and (orientation: portrait) {
    .mess_orientation {
        display: block;
        z-index: 100;
        /* background-color: aqua; */
    }
}